"use client";

import { useEffect, useRef, useState } from "react";
import { useParams } from "next/navigation";
import io, { Socket } from "socket.io-client";
import Canvas from "@/components/Canvas";
import { Canvas as FabricJSCanvas, Object as FabricObject, Path as FabricPath } from "fabric";
import { toast } from "sonner";
import { Pencil, Hand, Undo2, Redo2, Sparkles, Monitor, Grid, ZoomIn, ZoomOut } from "lucide-react";
import PageSidebar from "@/components/PageSidebar";
import { v4 as uuid } from "uuid";


export default function Board() {
    const { roomId } = useParams();
    const canvasRef = useRef<FabricJSCanvas | null>(null);
    const socketRef = useRef<Socket | null>(null);
    const pcRef = useRef<RTCPeerConnection | null>(null);
    const videoRef = useRef<HTMLVideoElement | null>(null);

    // UI / drawing state
    const [isStreaming, setIsStreaming] = useState(false);
    const [viewerCount, setViewerCount] = useState(0);
    const [aiPrompt, setAiPrompt] = useState("");
    const [isDrawingMode, setIsDrawingMode] = useState(true);
    const [brushColor, setBrushColor] = useState("#000000");
    const [brushWidth, setBrushWidth] = useState(3);
    const [viewUrl, setViewUrl] = useState(""); // set only on client
    const [showGrid, setShowGrid] = useState(true); // Figma-style grid toggle

    const canvasComponentRef = useRef<any>(null);

    // Undo / redo stacks (store Fabric objects)
    const undoStack = useRef<FabricObject[]>([]);
    const redoStack = useRef<FabricObject[]>([]);

    // Pages state
    const [pages, setPages] = useState<PageData[]>([
        { id: uuid(), name: "Page 1", canvasData: null },
    ]);
    const [activePageId, setActivePageId] = useState(pages[0].id);

    // --- Page management handlers ---
    const handleAddPage = () => {
        const newPage = { id: uuid(), name: `Page ${pages.length + 1}`, canvasData: null };
        setPages((prev) => [...prev, newPage]);
        setActivePageId(newPage.id);
    };

    const handleRenamePage = (id: string, newName: string) => {
        setPages((prev) =>
            prev.map((p) => (p.id === id ? { ...p, name: newName } : p))
        );
    };

    const handleDeletePage = (id: string) => {
        setPages((prev) => prev.filter((p) => p.id !== id));
        if (activePageId === id && pages.length > 1) {
            setActivePageId(pages.find((p) => p.id !== id)!.id);
        }
    };



    // --- Safe window usage (SSR-safe) ---
    useEffect(() => {
        if (typeof window !== "undefined") {
            setViewUrl(`${window.location.origin}/board/${roomId}/view`);
        }
    }, [roomId]);

    // --- Socket.io setup ---
    useEffect(() => {
        const socket = io(process.env.NEXT_PUBLIC_SIGNALING_URL || "http://localhost:3000");
        socketRef.current = socket;

        socket.on("connect", () => {
            socket.emit("joinSession", roomId);
        });

        socket.on("viewerCount", (count: number) => {
            setViewerCount(count);
        });

        return () => {
            socket.disconnect();
        };
    }, [roomId]);

    // --- WebRTC streaming (canvas + optional webcam) ---
    useEffect(() => {
        if (!isStreaming || !canvasRef.current || !videoRef.current) return;

        const pc = new RTCPeerConnection({
            iceServers: [{ urls: "stun:stun.l.google.com:19302" }],
        });
        pcRef.current = pc;

        // get actual canvas element (some Fabric versions expose lowerCanvasEl)
        const canvasElement =
            // @ts-expect-error - try known properties
            (canvasRef.current as any).lowerCanvasEl ?? (canvasRef.current as any).getElement?.();

        if (!canvasElement || typeof canvasElement.captureStream !== "function") {
            toast.error("Streaming not supported: unable to capture canvas stream.");
            return;
        }

        const canvasStream = canvasElement.captureStream(30);

        navigator.mediaDevices
            .getUserMedia({ video: true, audio: true })
            .then((webcamStream) => {
                const combined = new MediaStream();
                canvasStream.getVideoTracks().forEach((t) => combined.addTrack(t));
                webcamStream.getVideoTracks().forEach((t) => combined.addTrack(t));
                webcamStream.getAudioTracks().forEach((t) => combined.addTrack(t));

                combined.getTracks().forEach((t) => pc.addTrack(t, combined));
                videoRef.current!.srcObject = combined;
            })
            .catch((err) => {
                console.error("webcam error", err);
                toast.error("Could not access webcam â€” streaming canvas only.");
                canvasStream.getTracks().forEach((t) => pc.addTrack(t, canvasStream));
            });

        pc.onicecandidate = (ev) => {
            if (ev.candidate) {
                socketRef.current?.emit("ice-candidate", { sessionId: roomId, candidate: ev.candidate });
            }
        };

        socketRef.current?.on("answer", async (answer: RTCSessionDescriptionInit) => {
            await pc.setRemoteDescription(new RTCSessionDescription(answer));
        });

        socketRef.current?.on("ice-candidate", async (candidate: RTCIceCandidateInit) => {
            if (pcRef.current) {
                await pcRef.current.addIceCandidate(new RTCIceCandidate(candidate));
            }
        });

        pc.createOffer().then((offer) => {
            pc.setLocalDescription(offer);
            socketRef.current?.emit("offer", { sessionId: roomId, offer });
        });

        return () => {
            pc.close();
            // âœ… Safe cleanup to avoid null crash
            if (videoRef.current) {
                videoRef.current.srcObject = null;
            }
        };
    }, [isStreaming, roomId]);

    // --- Path created (for undo stack) ---
    const handlePathCreated = (path: FabricPath) => {
        if (canvasRef.current && path) {
            undoStack.current.push(path as unknown as FabricObject);
            redoStack.current = [];
        }
    };

    // Undo / Redo
    const handleUndo = () => {
        if (!canvasRef.current) return;
        const last = undoStack.current.pop();
        if (last) {
            redoStack.current.push(last);
            canvasRef.current.remove(last);
            canvasRef.current.requestRenderAll?.();
        }
    };
    const handleRedo = () => {
        if (!canvasRef.current) return;
        const last = redoStack.current.pop();
        if (last) {
            undoStack.current.push(last);
            canvasRef.current.add(last);
            canvasRef.current.requestRenderAll?.();
        }
    };

    const handleEnhance = () => {
        toast.info("AI enhancement not implemented yet.");
    };

    const copyLink = () => {
        if (!viewUrl) return;
        navigator.clipboard.writeText(viewUrl);
        toast.success("View link copied to clipboard!");
    };

    return (
        <div className="relative w-screen h-screen bg-neutral-100">
            {/* --- Header (slim Figma-style) --- */}
            <header className="fixed top-0 left-0 right-0 h-14 z-40 flex items-center justify-between px-4 bg-white/80 backdrop-blur-md border-b border-gray-200">
                <div className="flex items-center gap-3">
                    <h1 className="text-sm font-semibold text-gray-800">Bezalel Board</h1>
                    <span className="text-xs text-gray-500">/ {roomId}</span>
                </div>

                <div className="flex items-center gap-2">
                    <input
                        type="text"
                        value={viewUrl}
                        readOnly
                        className="text-xs text-gray-800 placeholder-gray-400 bg-gray-50 border border-gray-200 rounded-md px-2 py-1 w-64 focus:outline-none focus:ring-2 focus:ring-blue-400"
                        aria-label="view link"
                    />
                    <button
                        onClick={copyLink}
                        className="inline-flex items-center gap-2 px-3 py-1 rounded-md bg-blue-600 text-white text-sm hover:bg-blue-700 transition"
                    >
                        Copy
                    </button>

                    <div className="text-xs text-gray-600 px-2">ðŸ‘€ {viewerCount} viewer{viewerCount === 1 ? "" : "s"}</div>
                </div>
            </header>

            {/* --- Main area: occupies rest of viewport under header --- */}
            <main className="absolute top-14 bottom-0 left-0 right-0 flex">
                

                {/* Canvas pane (center) */}
                <div className="flex-1 flex items-center justify-center overflow-auto">
                    <Canvas
                        ref={canvasComponentRef}
                        isDrawingMode={isDrawingMode}
                        setCanvasRef={(c) => (canvasRef.current = c)}
                        onPathCreated={handlePathCreated}
                        width={5000}
                        height={3500}
                        brushColor={brushColor}
                        brushWidth={brushWidth}
                        showGrid={showGrid}
                    />
                </div>

                {/* optional picture-in-picture preview */}
                {isStreaming && (
                    <aside className="w-64 p-3 border-l border-gray-100 bg-white/60">
                        <video ref={videoRef as any} autoPlay muted className="w-full h-40 rounded-md object-cover" />
                    </aside>
                )}
            </main>

            {/* --- Floating toolbar (bottom center) Figma-style slim --- */}
            <div className="fixed bottom-6 left-1/2 -translate-x-1/2 z-50">
                <div className="flex items-center gap-2 rounded-lg bg-white/95 backdrop-blur-md border border-gray-200 shadow-md px-2 py-2">
                    {/* Draw / Pan toggle */}
                    <button
                        title={isDrawingMode ? "Drawing" : "Pan"}
                        onClick={() => setIsDrawingMode((s) => !s)}
                        className={`p-2 rounded-md hover:bg-gray-100 transition ${isDrawingMode ? "bg-gray-100" : ""}`}
                    >
                        {isDrawingMode ? <Pencil className="w-4 h-4 text-gray-800" /> : <Hand className="w-4 h-4 text-gray-800" />}
                    </button>

                    {/* Color */}
                    <input
                        title="Brush color"
                        type="color"
                        value={brushColor}
                        onChange={(e) => setBrushColor(e.target.value)}
                        className="w-7 h-7 rounded-md border border-gray-200"
                    />

                    {/* Width slider (compact) */}
                    <input
                        title="Brush size"
                        type="range"
                        min={1}
                        max={40}
                        value={brushWidth}
                        onChange={(e) => setBrushWidth(Number(e.target.value))}
                        className="w-24 accent-blue-500"
                    />

                    <div className="w-px h-6 bg-gray-200 mx-2" />

                    {/* Undo / Redo controls */}
                    <button title="Undo" onClick={handleUndo} className="p-2 rounded-md hover:bg-gray-100">
                        <Undo2 className="w-4 h-4 text-gray-800" />
                    </button>
                    <button title="Redo" onClick={handleRedo} className="p-2 rounded-md hover:bg-gray-100">
                        <Redo2 className="w-4 h-4 text-gray-800" />
                    </button>

                    <div className="w-px h-6 bg-gray-200 mx-2" />

                    {/* inside zoom in and out controls*/}
                    <button
                        title="Zoom Out"
                        onClick={() => canvasComponentRef.current?.zoomOut()}
                        className="p-2 rounded-md hover:bg-gray-100"
                    >
                        <ZoomOut className="w-4 h-4 text-gray-800" />
                    </button>
                    <button
                        title="Zoom In"
                        onClick={() => canvasComponentRef.current?.zoomIn()}
                        className="p-2 rounded-md hover:bg-gray-100"
                    >
                        <ZoomIn className="w-4 h-4 text-gray-800" />
                    </button>

                    {/* AI prompt + enhance */}
                    <input
                        title="AI prompt"
                        placeholder="AI prompt"
                        value={aiPrompt}
                        onChange={(e) => setAiPrompt(e.target.value)}
                        className="text-xs text-gray-800 placeholder-gray-400 bg-gray-50 border border-gray-200 rounded-md px-2 py-1 w-44 focus:outline-none focus:ring-1 focus:ring-green-400"
                    />
                    <button title="Enhance" onClick={handleEnhance} className="p-2 rounded-md hover:bg-green-50">
                        <Sparkles className="w-4 h-4 text-green-600" />
                    </button>

                    <div className="w-px h-6 bg-gray-200 mx-2" />

                    {/* Grid toggle (Figma-style) */}
                    <button
                        title="Toggle grid"
                        onClick={() => setShowGrid((s) => !s)}
                        className={`p-2 rounded-md hover:bg-gray-100 transition ${showGrid ? "bg-gray-100" : ""}`}
                    >
                        <Grid className={`w-4 h-4 ${showGrid ? "text-gray-800" : "text-gray-500"}`} />
                    </button>

                    <div className="w-px h-6 bg-gray-200 mx-2" />

                    {/* Streaming toggle */}
                    <label className="flex items-center gap-2 text-xs text-gray-700 cursor-pointer">
                        <input
                            type="checkbox"
                            checked={isStreaming}
                            onChange={(e) => setIsStreaming(e.target.checked)}
                            className="w-4 h-4 accent-blue-500"
                        />
                        <Monitor className="w-4 h-4 text-gray-700" />
                    </label>
                </div>
            </div>
        </div>
    );
}
